.studies {
  margin-bottom: 48px;
  padding: 48px 0;
  border-bottom: 1px solid var(--slate-200);
  border-top: 1px solid var(--slate-200);
  @include mobile{
    padding: 40px 0;
    margin-bottom: 40px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include tablet{
      flex-direction: column;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
    min-width: clamp(22.5rem, -21.136rem + 68.18vw, 30rem);
    padding: 24px 40px;
    @include tablet{
      padding: 0;
    }
  }

  &__info-top {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__title {
  }

  &__swiper-num{
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--slate-700);
  }

  &__navigation-buttons {
  }
}
