.studies-swiper {
  min-width: 0;
  @include tablet{
    margin: 0;
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 560px;
    position: relative;
    overflow: hidden;
    border-radius: 24px;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 40px;
    &::after{
      display: block;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: linear-gradient(360deg, #d9d9d9 26.41%, rgba(172, 172, 172, 0.35) 56.48%, rgba(115, 115, 115, 0) 77.89%);
      z-index: 1;
    }
    @include mobile{
      height: 360px;
      padding: 20px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 133%;
    color: var(--slate-950);
    margin: 0;
    margin-bottom: 12px;
    position: relative;
    z-index: 2;
    @include mobile{
      font-size: 20px;
    }
  }

  &__deskr {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--slate-700);
    margin: 0;
    position: relative;
    z-index: 2;
    max-width: 480px;
  }
}
