.product-list {
  margin-bottom: 96px;
  @include small-tablet {
    padding-left: 20px;
    margin-bottom: 48px;
  }
  // .product-list__upper

  &__swiper-item {
    display: flex;
    flex-direction: column;
    border: 1px solid var(--blue---500);
    border-radius: 20px;
    position: relative;
    padding: 20px;
    transition: 0.2s ease-in;
    &::before {
      content: "";
      border-radius: 20px;
      position: absolute;
      top: -1px;
      left: -1px;
      right: -1px;
      bottom: -1px;
      border: 2px solid transparent;
      pointer-events: none;
    }
    @include hover {
      &::before {
        transition: 0.2s ease-in;
        border: 2px solid var(--blue---900);
      }
    }
  }
  &__upper {
    display: flex;
    gap: 24px;
    margin: 0 auto;
    align-items: center;
    width: 630px;
    margin-bottom: 44px;
    @include small-tablet {
      width: unset;
      padding: 0;
      gap: 16px;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 24px;
    }
  }

  // .product-list__title

  &__title {
    margin: 0;
    @include mobile {
      text-align: unset;
    }
  }

  // .product-list__link

  &__link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---0);
    border-radius: 16px;
    padding: 12px 24px;
    background: var(--blue---500);
  }

  // .product-list__swiper

  &__swiper {
    gap: 20px;
  }

  // .product-list__item-logo

  &__item-logo {
    border-radius: 16px;
    background: var(--slate-100);
    padding: 6px 10px;
    top: 12px;
    left: 12px;
    position: absolute;
  }

  // .product-list__item-img

  &__item-img {
  }

  // .product-list__item-title

  &__item-title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 144%;
    color: var(--blue---1000);
    margin: 0;
    margin-bottom: 8px;
  }

  // .product-list__item-desk

  &__item-desk {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: #707176;
    margin-bottom: 10px;
  }

  // .product-list__item-sub

  &__item-sub {
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    @include mobile{
      flex-wrap: wrap;
    }
  }

  // .product-list__item-price

  &__item-price {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    color: var(--blue---1000);
  }

  // .product-list__item-link

  &__item-link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---0);
    padding: 8px 16px;
    border-radius: 12px;
    background: var(--blue---500);
  }
}
