.stats {
    padding: 48px 0;
    @include small-tablet{
        padding: 24px;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);

        gap: 24px;
        @include small-tablet{
            grid-template-columns: 1fr;

        }
    }


    &__item {
        display: flex;
        flex-direction: column;
        gap: 16px;
        justify-content: center;
        align-items: center;

    }



    &__item-num {
        font-family: var(--third-family);
        font-weight: 400;
        font-size: 64px;
        line-height: 112%;
        letter-spacing: -0.04em;
        text-align: center;
        color: var(--blue---650);
        @include small-tablet{
            font-size: 48px;
            line-height: 67%;
            letter-spacing: unset;
        }
    }



    &__item-desk {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 18px;
        line-height: 144%;
        text-align: center;
        color: var(--slate-700);
    }
}


