.brand{
    margin-bottom: 96px;
    @include small-tablet{
        margin-bottom: 48px;
    }
    &__title{
        text-align: center;
        margin: 0;
        margin-bottom: 40px;
        @include small-tablet{
            text-align: left;
            margin-bottom: 24px;
        }
    }
    &__wrapper{

    }
    &__list{
        overflow-x: hidden;
        gap: 16px;
        display: grid;
        align-items: center;
        grid-template-columns: repeat(6, 1fr);
        @include small-tablet{
            margin-right: -20px;
            display: flex;
            flex-wrap: nowrap;
            overflow-x: auto;
            -ms-overflow-style: none;  /* Internet Explorer 10+ */
            scrollbar-width: none;  /* Firefox */
        }
    }
    &__content{

    }
    &__item{
        border-radius: 16px;
        background: #f8f8f8;
        height: 106px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include small-tablet{
            flex: 0 0 calc(100% / 1.5 - 10px);
        }
    }
}
