.products {
  // .products__wrapper
  margin-bottom: 100px;
  @include mobile {
    margin-bottom: 48px;
  }
  // &__wrapper {
  //   padding-top: 80px;
  //   @include small-tablet {
  //     padding-top: 40px;
  //   }
  // }

  &__turenhersteller{

  }


  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 36px;
    line-height: 111%;
    text-align: center;
    color: var(--blue---650);
    text-align: center;
    @include small-tablet {
      margin-bottom: 20px;
      text-align: left;
    }
  }

  // .products__list

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 20px;
    @include tablet {
      grid-template-columns: 1fr;
    }
    @include small-tablet {
      gap: 16px;
    }
  }

  // .products__item

  &__item {
    position: relative;
    height: 480px;
    border-radius: 24px;
    background: var(--black);
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-position: center;
    background-size: cover;
    &:nth-child(1) {
      grid-column: span 2;
    }
    &:nth-child(2) {
      grid-column: span 2;
    }
    &:nth-child(3) {
      grid-column: span 2;
    }
    &:nth-last-child(-n + 2) {
      grid-column: span 3;
      @include small-tablet {
        grid-column: span 2;
      }
    }
    @include mobile {
      height: 400px;
      padding: 20px;
    }
    &::after {
      content: "";
      border-radius: 24px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        to top right,
        rgba(0, 0, 0, 1),
        rgba(0, 0, 0, 0)
      );
      pointer-events: none;
      z-index: 1;
    }
  }

  // .products__item-title

  &__item-title {
    margin: 0;
    margin-bottom: 12px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;
    line-height: 133%;
    color: var(--white);
    @include mobile {
      font-size: 20px;
      line-height: 140%;
    }
    z-index: 5;
  }

  // .products__item-text

  &__item-text {
    z-index: 5;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--slate-100);
    margin-bottom: 24px;
  }

  // .products__item-btns

  &__item-btns {
    z-index: 5;
  }

  // .products__btn

  &__btn {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;

  }

  // .products__link

  &__link {
    padding: 13px 21px;
    background: var(--blue---0);
    border-radius: 16px;
  }
}

.text{
  p{
    padding: 64px 0;
    margin: 0 auto;
    max-width: 960px;
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 44px;
    line-height: 109%;
    text-align: center;
    color: var(--blue---650);
  }
}

.text-left{
p{
  max-width: 1200px;
  padding: 0px 48px 40px;
  text-align: left;
  margin: 0;
  @include tablet{
    padding: 24px 0px 40px;
    font-size: 24px;
    line-height: 133%;
  }
}

}
