.newsletter {
  padding: 64px 0;
  border-bottom: 1px solid var(--blue---500);
  border-top: 1px solid var(--blue---500);
  margin-bottom: 40px;
  &__content{
    max-width: 560px;
    margin: 0 auto;
  }

  &__title {
    text-align: center;
    margin-bottom: 32px;
    @include mobile{
      margin-bottom: 24px;
    }
  }

  &__form {
    display: flex;
    justify-content: center;
    gap: 12px;
    margin-bottom: 12px;
    @include mobile{
      flex-direction: column;
    }
  }

  &__form-input{
    width: 100%;
    max-width: 390px;
  }

  &__form-button {
  }

  &__form-personal{
    text-align: center;
    display: block;
  }
}
