.installationspartner {
    margin-bottom: 48px;

    &__content {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 40px;
        align-items: center;
        @include tablet{
            grid-template-columns: 1fr;
        }
    }

    &__link{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---500);
    padding: 12px 24px;
    border: 1px solid var(--blue---500);
    border-radius: 16px;
    }



    &__text {
        max-width: 600px;
        padding-left: 40px;
        @include tablet{
            max-width: unset;
        }
    }



    &__title {
        margin: 0;
        margin-bottom: 32px;
    }



    &__desk {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: var(--blue---1000);
    }
    &__img{
        border-radius: 24px;
        @include tablet{
            width: 100%;
        }
    }
}

.bottom32{
    margin-bottom: 32px;
}
