.reviews {
  margin-bottom: 48px;
  @include mobile{
    margin-bottom: 40px;
  }
  &__content {
    display: flex;
    border-radius: 24px;
    padding: 40px;
    gap: 40px;
    background-color: #f0f4f7;
    @include tablet{
      flex-direction: column;
    }
    @include mobile{
      padding: 20px;
      gap: 24px;
    }
  }

  &__imgs {
    min-width: clamp(22.5rem, -50.227rem + 113.64vw, 35rem);
    min-height: clamp(22.5rem, -35.682rem + 90.91vw, 32.5rem);
    @include tablet{
      text-align: center;
      min-width: unset;
      min-height: unset;
      width: 100%;
    }
  }

  &__img {
    border-radius: 20px;

  }

  &__swiper-wrapper {
    flex: 1 1 auto;
    min-width: 0;
    display: flex;
    flex-direction: column;
    @include tablet{
      gap: 20px;
    }

  }

  &__navigation {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include mobile{
      flex-direction: column;
      gap: 24px;
    }

  }

  &__navigation-buttons{
    @include mobile{
      justify-content: flex-end;
    }
  }

  &__google-link {
  }
}
.container {
}
.swiper {
}
.review-text-swiper {
  flex: 1 1 auto;
  margin: 0;
  &__item {
  }

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 36px;
    line-height: 100%;
    color: var(--blue---1000);
    margin-bottom: 24px;
    @include small-tablet{
      font-size: 20px;
    }
  }

  &__author {
    font-weight: 500;
    font-size: 18px;
    color: var(--blue---1000);
    position: relative;
    padding-left: 28px;
    display: block;
    &::after{
      content: '';
      display: block;
      width: 16px;
      height: 2px;
      background-color: var(--blue---500);
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
