html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}
.request{
  display: inline-block;
  padding: 12px 24px;
  border: 1px solid var(--blue---500);
  border-radius: 16px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  color: var(--blue---500);
}
img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-right {
  padding-left: max(20px, (100% - 1520px) / 2);
}


.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title-h2 {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 36px;
  color: var(--blue---650);
  margin: 0;
  @include small-tablet{
    font-size: 28px;
  }
}

.btn-blue {
  font-weight: 500;
  font-size: 16px;
  color: var(--white);
  border-radius: 16px;
  padding: 12px 24px;
  background-color: var(--blue---500);
  display: inline-block;
}

.accent-text {
  color: var(--blue---650);
}

.input-style {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #8d8e91;
  border-radius: 16px;
  padding: 12px 16px;
  background-color: #eff2f4;
}

.paragraph{
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--blue---1000);
}

.text-content{
  @include mobile{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 5;
            line-clamp: 5;
    -webkit-box-orient: vertical;
    &--expanded{
      display: block;
    }
  }
}

.show-more-text-btn{
  display: none;
  @include mobile{
    display: block;
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---500);
  }
}

.adaptive-mobile {
  display: none !important;
  z-index: 5;
  @include small-tablet {
    display: flex !important;
    flex-direction: row !important;
  }
}

.is-mobile {
  display: none;
  @include small-tablet {
    display: block;
  }
}

.is-tablet {
  display: none;
  @include tablet {
    display: block;
  }
}

.btn-border {
  font-weight: 500;
  font-size: 16px;
  color: var(--blue---500);
  border: 1px solid var(--blue---500);
  border-radius: 16px;
  padding: 12px 24px;
  text-align: center;
  display: inline-block;
}

