.hero {
  position: relative;
  background: #1e323e;
  height: 100vh;
  background-size: cover;
  background-position: right center;
  margin-bottom: 96px;
  @include mobile {
    height: auto;
    margin-bottom: 48px;
  }
  &__pict {
    width: 100%;
    z-index: 1;
    top: 0;
    right: 0;
    position: absolute;
    @include mobile {
      position: unset;
    }
  }
  &__img {
    width: 100%;
    height: 100vh;
    @include mobile {
      height: auto;
      width: 100%;
    }
  }
  &__wrapper {
    height: 100vh;
    padding-left: 40px;
    padding-bottom: 80px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    @include mobile {
      height: auto;
      padding-left: 0px;
      padding-bottom: 40px;
    }
  }

  &__content {
    z-index: 5;
  }
  &__info {
    max-width: 530px;
    @include mobile {
      max-width: unset;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 32px;
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 64px;
    line-height: 112%;
    letter-spacing: -0.04em;
    color: var(--blue---0);
    @include mobile {
      font-family: var(--third-family);
      font-weight: 400;
      font-size: 36px;
      line-height: 111%;
      color: var(--blue---0);
    }
  }

  &__desk {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: var(--blue---0);
    margin-bottom: 48px;
    @include mobile {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--blue---0);
    }
  }


  &__content-links{
    display: flex;
    gap: 12px;
  }
  &__content-link{
    display: inline-block;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---0);
    padding: 12px 24px;
    background: var(--blue---500);
    border-radius: 16px;
  }
  .turenhersteller-info{
    max-width: 660px;
  }
  .turenhersteller-desk{
    max-width: 480px;
  }

  .turenhersteller-pict{
    left: 70%;
    top: 50%;
  @include small-tablet{
    top: 188px;
    left: 50%;
    right: unset;
    width: unset;
    position: absolute;
transform: translate(-50%, -50%);
  }

}
  .turenhersteller-img{
    width: unset;
    height: unset;
  }
  .turenhersteller-wrapper{
  height: 100vh;
  }
}


