.advantages {
  margin-bottom: 64px;
  &__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-content: space-between;
    gap: 24px;
    max-width: 1160px;
    margin: 0 auto;
    @include tablet{
      overflow: auto;
      gap: 12px;
      grid-template-columns: repeat(5, 156px);
      -ms-overflow-style: none;  /* Internet Explorer 10+ */
      scrollbar-width: none;  /* Firefox */
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    color: var(--blue---1000);
  }
}
