.header {
  top: 8px;
  position: absolute;
  width: 100%;
  z-index: 5;
  &__navigation {
    justify-content: center;
    padding: 26px 32px;
    display: flex;
    gap: 48px;
    align-items: center;
    background-color: var(--white);
    border: 1px solid var(--blue---500);
    border-radius: 8px;
    @include small-tablet {
      padding: 22px 23px;
      gap: 24px;
      margin: 0 -16px;
    }
  }
  &__logo {
    max-width: 92px;

    @include small-tablet {
      display: none;
    }
  }
}

.list-reset {
}

.menu {
  display: flex;
  justify-content: space-between;
  width: 100%;
  @include small-tablet {
    height: 0;
    visibility: hidden;
    border: 1px solid var(--blue---500);
    border-radius: 8px;
    padding: 24px 20px;
    position: fixed;
    background-color: white;
    top: 8px;
    left: 4px;
    right: 4px;
    width: unset;
    padding-top: 84px;
    display: flex;
    justify-content: unset;
    flex-direction: column;
    transition: all 0.2s linear;
    overflow: hidden;
    &--active {
      overflow: auto;
      visibility: visible;
    }
  }
  &__link-shop {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);
    margin-bottom: 24px;
  }
  &__item {
    gap: 12px;
    @include small-tablet {
      display: flex;
    }
  }

  &__list {
    display: flex;
    column-gap: 30px;
    @include small-tablet {
      width: 100%;
      flex-direction: column;
      row-gap: 8px;
      text-align: center;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 12px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);
  }
  &__languages {
    @include small-tablet {
      margin-top: 48px;
      display: flex;
      gap: 16px;
      align-items: center;
    }
  }
  &__languages-de {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);
    display: flex;
    align-items: center;
    gap: 12px;
  }
  &__languages-en {
    opacity: 0.5;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);
    display: flex;
    align-items: center;
    gap: 12px;
  }
}
.burger {
  @include burger;
  display: none;
  @include small-tablet {
    display: block;
  }
}

.notice {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    transform: translateY(-50%);
    top: 3px;
    right: 0;
    background-image: url(../img/icon-notice.svg);
    width: 11px;
    height: 11px;
    @include small-tablet {
      top: 7px;
      right: -1px;
    }
  }
}

.menu-mobile {
  display: flex;
  @include small-tablet {
    display: none;
  }
}
.menu-mobile-item {
  @include small-tablet {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 0;
    border-bottom: 1px solid var(--slate-300);
  }
}
.link-mobile {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.logo-menu {
  justify-content: center;
  flex-grow: 1;
}
