.footer {
  background: var(--blue---650);
  padding: 48px 0 32px 0;
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 48px;
    @include mobile{
      flex-wrap: wrap;
      gap: 16px;
    }
  }

  &__logo {

  }

  &__logo-img {
  }

  &__contacts {
    display: flex;
    align-items: center;
    gap: 40px;
  }

  &__contacts-links {
    display: flex;
    flex-direction: column;
    gap: 6px;
    @include mobile{
      display: none;
    }
    &--mobile{
      display: none;
      @include mobile{
        display: flex;
        width: 100%;
        align-items: flex-start;
      }
    }
  }

  &__contacts-link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    text-align: right;
    color: var(--blue---0);
  }

  &__contacts-socials {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  &__contacts-social {

  }

  &__contacts-social-link {
    width: 40px;
    height: 40px;
    background: var(--blue---0);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: all .2s ease-in;
    @include mobile{
      width: 36px;
      height: 36px;
    }
    svg{
      transition: all .2s ease-in;
    }
    @include hover{
      background: var(--blue---250);
        svg{
          path{
            fill: #003953 !important;
          }
        }
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
    gap: 32px;
    margin-bottom: 80px;
    @include tablet{
      flex-direction: column;
    }
  }

  &__nav {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 32px;
    width: 100%;
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
    @include mobile{
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__nav-column {
    @include mobile{
      padding: 24px 0;
      border-bottom: 1px solid var(--blue---500);
      &:last-child{
        padding-bottom: 0;
        border: none;
      }
      &:first-child{
        padding-top: 0;
      }
      &--expanded{
        .footer__nav-column-title{
          margin-bottom: 20px;
        }
        .footer__nav-list{
          display: flex;
        }
      }
    }
  }

  &__nav-column-3{
    grid-column: 3 span;
    @include tablet{
      grid-column: 2 span;
    }
    @include mobile{
      grid-column: 1;
    }
  }

  &__nav-column-title {
    font-weight: 700;
    font-size: 18px;
    color: var(--blue---200);
    margin-bottom: 20px;
    display: block;
    @include mobile{
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: var(--blue---0);
      margin: 0;
    }
  }

  &__nav-drop-button{
    font-size: 24px;
    color: white;
    display: none;
    @include mobile{
      display: block;

    }
  }

  &__nav-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
    &-col-3{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      column-gap: 32px;
      @include tablet{
        grid-template-columns: repeat(2, 1fr);
      }
      @include mobile{
        grid-template-columns: 1fr;
      }
    }
    @include mobile{
      display: none;
    }
  }

  &__nav-item {
  }

  &__nav-link {
    font-weight: 400;
    font-size: 16px;
    color: var(--blue---0);
    transition: color .2s ease-in;
    @include hover{
      color: var(--blue---200);
    }
    @include mobile{
      color: var(--blue---100);
    }
  }

  &__banner-link {
  }

  &__banner-img {
    border: 2px solid #e8e5dc;
    border-radius: 12px;
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    @include mobile{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__coppyright {
    display: flex;
    align-items: center;
    gap: 24px;
    @include mobile{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__coppyright-text {
    font-weight: 400;
    font-size: 16px;
    color: var(--blue---0);
    opacity: 0.6;
    @include mobile{
      opacity: 1;
    }
  }

  &__payment-methods-list {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  &__payment-method-item {
  }

  &__payment-method-img {
    border-radius: 8px;
    width: 48px;
    height: 32px;
  }

  &__bottom-links {
    display: flex;
    gap: 48px;
  }

  &__bottom-link {
    font-weight: 400;
    font-size: 16px;
    text-transform: capitalize;
    color: var(--blue---0);
    opacity: 0.6;
    @include mobile{
      opacity: 1;
    }
  }
}
