.comparison-table{
  margin-bottom: 96px;
  @include mobile{
    margin-bottom: 48px;
  }
  &__title{
    text-align: center;
    margin-bottom: 40px;
  }
  &__wrapper{
    overflow: auto;
  }
}

.comparison-table__table {
  width: 100%;
  border: 0;
  overflow: hidden;
  border-collapse: collapse;
  width: 1520px;
  th {
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    color: var(--slate-900);
    padding: 0 24px 24px 24px;
    text-align: center;
    font-size: 18px;
    &:not(:first-child) {
      border-left: 1px solid var(--slate-200);
      border-right: 1px solid var(--slate-200);
    }
    &:last-child {
      border: 0;
    }
    span {
      margin-top: 12px;
      display: inline-block;
    }
  }

  td {
    padding: 12px 16px;
    border: 1px solid var(--slate-200);
    text-align: center;
    &:first-child {
      border-left: 0;
    }
    &:last-child {
      border-right: 0;
    }
    border-bottom: 0;

  }

  .labels tr td {
    background-color: var(--slate-100);
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    color: var(--slate-900);
    &.comparison-table__text-700{
      text-align: left;
    }
  }

  .labels svg{
    transition: all .2s ease-in;
  }

  .labels--close{
    svg{
      transform: rotate(180deg);
    }
  }

  .label tr td label {
    display: block;
  }

  [data-toggle="toggle"] {
    display: none;
  }

  .comparison-table__input {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    width: 0;
    height: 0;
    z-index: -99999;
    top: 10000px;
  }
  label {
    display: flex;
    gap: 10px;
  }
  .comparison-table__img {
    width: 200px;
    height: 200px;
  }
  .comparison-table__text-700 {
    font-weight: 700;
  }

  .comparison-table__link {
    margin-top: 12px;
  }

  .price {
    span {
      font-weight: 700;
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color: var(--slate-900);
      display: block;
    }
  }
  .comparison-table__text-500{
    font-weight: 500;
    text-align: left;
  }
  .comparison-table__text-700{
    font-weight: 700;
    text-align: left;
  }
}
