.popular-product {
  margin-bottom: 48px;
  &__content {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include mobile{
      flex-direction: column;
    }
  }

  &__wrapper {
    flex: 1;
    width: 100%;
    max-width: 1050px;
    border: 1px solid var(--blue---500);
    border-radius: 24px;
    padding: 40px;
    @include big-desktop{
      max-width: 100%;
      padding: 20px;
      border-radius: 20px;
    }
  }

  &__title{
    margin-bottom: 44px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @include mobile{
      flex-direction: column;
      gap: 16px;
      margin-bottom: 20px;
    }
  }

  &__img{
    border-radius: 24px;
    width: clamp(18.75rem, -73.821rem + 102.86vw, 27.75rem);
    @include big-desktop{
      display: none;
    }
    @include mobile{
      display: block;
      width: 100%;
      height: 390px;
    }
  }
}
