.uber {
  margin-bottom: 80px;
  &__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    @include tablet{
      align-items: center;
    }
    @include small-tablet{
      grid-template-columns: 1fr;
    }
  }

  &__info {
    padding: 32px 40px;
    @include mobile{
      padding: 0;
    }
  }

  &__uptitle {
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---400);
    margin-bottom: 16px;
    display: block;
    @include mobile{
      font-size: 14px;
    }
  }

  &__title {
    margin-bottom: 32px;
  }

  &__deskr {
    margin: 0;
    margin-bottom: 32px;

  }

  &__link {
  }

  &__img {
    border-radius: 24px;
  }
}

.gefunden {

  &__content {
    display: flex;
    gap: 100px;
    @include small-tablet{
      flex-direction: column;
    }
  }

  &__info {
    max-width: 410px;
    padding-top: 50px;
    padding-left: 40px;
    @include mobile{
      padding: 0;
    }
  }

  &__title {
    margin-bottom: 24px;
  }

  &__deskr {
    margin-bottom: 32px;
  }

  &__link {
  }

  &__img {
    width: clamp(18.75rem, -70.293rem + 139.13vw, 42.75rem);
    @include small-tablet{
      width: 75%;
      align-self: center;
    }
    @include mobile{
      width: 100%;
      margin: 0 -20px;
    }
  }
}

