.improvement {
  margin-bottom: 96px;
  @include mobile {
    margin-bottom: 48px;
  }
  &__title {
    text-align: center;
    margin: 0;
    margin-bottom: 40px;
    @include small-tablet {
      text-align: left;
    }
    @include mobile {
      margin-bottom: 24px;
      font-size: 28px;
      line-height: 114%;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    overflow-x: hidden;
    @include small-tablet {
      grid-template-columns: 1fr;
      gap: 10px;
      padding: 0px;
    }
  }
.turenhersteller-adaptive{
  @include small-tablet{
    margin-right: -20px;
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
  }

}
  &__item {
    height: 490px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding: 40px;
    border-radius: 24px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include small-tablet {
      padding: 20px;
      height: 400px;
    }
  }
.turenhersteller-item-adaptive{
  @include small-tablet{
    flex: 0 0 calc(100% / 1.1 - 10px);
}
}
  &__item-title {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 28px;
    line-height: 129%;
    margin: 0;
    margin-bottom: 16px;
    color: var(--blue---1000);
    @include small-tablet {
      font-size: 20px;
      line-height: 140%;
    }
  }

  &__item-desk {
    max-width: 300px;
    margin: 0;
    margin-bottom: 24px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);
  }

  &__item-link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---0);
    padding: 12px 24px;
    border-radius: 16px;
    background: var(--blue---500);
    @include small-tablet {
      border-radius: 12px;
      padding: 8px 16px;
    }
  }

}


