.faq{
  margin-bottom: 96px;
  @include mobile{
    margin-bottom: 46px;
  }
  &__title{
    text-align: center;
    margin-bottom: 22px;
    @include mobile{
      text-align: start;
      margin-bottom: 6px;
    }
  }
}

.accordion{
  max-width: 1000px;
  margin: 0 auto;
}

.accordion__item {
  border-bottom: 1px solid var(--slate-200);;
}

.accordion__button {
  position: relative;
  display: block;
  text-align: left;
  width: 100%;
  padding: 1em 0;
  color: #7288a2;
  font-size: 1.15rem;
  font-weight: 400;
  border: none;
  background: none;
  outline: none;
}

.accordion__button[aria-expanded="true"] .accordion__icon{
  transform: rotate(45deg);
}


.accordion__button:hover,
.accordion__button:focus {
  cursor: pointer;
  color: #03b5d2;
}

.accordion__button:hover::after,
.accordion__button:focus::after {
  cursor: pointer;
  color: #03b5d2;
  border: 1px solid #03b5d2;
}

.accordion__title {
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: #171717;
  max-width: 750px;
  display: block;
  padding-right: 40px;
}

.accordion__icon {
  display: inline-block;
  position: absolute;
  top: 18px;
  right: 0;
  width: 32px;
  height: 32px;
  transition: all .2s ease-in;
  display: flex;
  align-items: center;
  justify-content: center;
}

.accordion__button[aria-expanded="true"] + .accordion__content {
  opacity: 1;
  max-height: 100%;
  transition: all 200ms linear;
  will-change: opacity, max-height;
}

.accordion__content {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 200ms linear, max-height 200ms linear;
  will-change: opacity, max-height;
}
.accordion__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--slate-700);
  margin: 0;
  margin-bottom: 20px;
}
