.infographic {
  padding-top: 64px;

  margin-bottom: 48px;
  @include tablet {
    padding-top: 0px;
    height: auto;
  }
  &__content {
    padding: 85px 120px;
    display: flex;
    align-items: center;
    position: relative;
    @include tablet {
      flex-direction: column;
      justify-content: center;
      padding: 40px 0px;
    }
  }

  &__img {
    width: clamp(15rem, 11.429rem + 5.58vw, 18.125rem);
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    position: absolute;
    @include tablet {
      position: unset;
      max-width: unset;
      transform: translateX(0);
      margin-bottom: 24px;
    }
  }

  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(6, 1fr);
    row-gap: clamp(1.563rem, -0.223rem + 2.79vw, 3.125rem);
    @include tablet {
      display: none;
    }
  }
  &__item {
    grid-column: span 2;
    z-index: 3;
    align-items: center;
    position: relative;
    display: flex;
    &:nth-child(even) {
      flex-direction: row-reverse;
      .infographic__item-line {
        &::before {
          left: -16px;
          transform: translate(-100%, -50%);
        }
      }
    }
    &:nth-child(2) {
      .infographic__item-line {
        width: clamp(4.188rem, -30.664rem + 51.33vw, 19.063rem);
      }
    }
    &:nth-child(3) {
      .infographic__item-line {
        width: clamp(1.875rem, -26.11rem + 43.73vw, 17.25rem);
      }
    }
    &:nth-child(4) {
      .infographic__item-line {
        width: clamp(0rem, -23.726rem + 37.07vw, 12.188rem);
      }
    }
    &:nth-child(5) {
      .infographic__item-line {
        width: clamp(0rem, -24.335rem + 38.02vw, 12.5rem);
      }
    }
    &:nth-child(6) {
      .infographic__item-line {
        width: clamp(1.688rem, -27.636rem + 45.82vw, 16.75rem);
      }
    }
  }
  &__item-line {
    width: clamp(4.188rem, -28.664rem + 51.33vw, 21.063rem);
    height: 2px;
    position: relative;
    background-image: url(../img/line.svg);
    &::before {
      content: "";
      background-repeat: no-repeat;
      background-image: url(../img/circle.png);
      width: clamp(1.375rem, -0.196rem + 2.46vw, 2.75rem);
      height: clamp(1.375rem, -0.196rem + 2.46vw, 2.75rem);
      background-size: contain;
      position: absolute;
      top: 50%;
      right: -16px;
      transform: translate(100%, -50%);
    }
  }
  &__item-name {
    max-width: 265px;
    display: flex;
    flex-direction: column;
  }

  &__item-title {
    margin: 0;
    margin-bottom: 8px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: var(--blue---1000);
    @include tablet {
      display: none;
    }
  }

  &__item-desk {

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: #434447;
    @include tablet {
      display: none;
    }
  }
  &__list-num{
    counter-reset: item;
    flex-direction: column;
    gap: 12px;
    display: none;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);

    @include tablet{
      align-self: flex-start;
      display: flex;
    }


  }
  &__item-num{
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: start;
    &::before{
      counter-increment: item;
      content: counter(item) ".";
      align-self: start;
      margin-right: 5px;
      margin-left: 5px;
    }
    &:nth-child(4) {
      counter-increment: item; // Увеличить счетчик на 1 для четвертого элемента
      content: counter(item) "."; // Отобразить нумерацию
    }

    &:nth-child(n+5)::before {
      counter-increment: item 1; // Увеличить счетчик на 1 начиная с пятого элемента
    }


  }
  &__item-num-title {
    margin: 0;
    margin-bottom: 4px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---1000);

  }

  &__item-num-desk {
    grid-column: span 2;
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: #434447;
  }
  &__content-img{
    @include tablet{
      position: relative;
    }

  }
  &__list-tablet{
    width: 355px;
    position: absolute;
    top:115px;
    left: 50%;
    transform: translateX(-50%);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(6, 1fr);
   row-gap: 27px;
    display: none;
      @include tablet{
       display: grid;
      }
  }
  &__item-tablet{
    grid-column: span 2;
    position: relative;
    display: flex;
    align-items: center;
    &:nth-child(even) {
      flex-direction: row-reverse;
      .infographic__item-line-tablet {
        &::before {
          left: -5px;
          transform: translate(-100%, -50%);
        }
      }
    }
    &:nth-child(1) {
      right: -40px;
      .infographic__item-line-tablet {
        width: 40px;
      }
    }
    &:nth-child(2) {
      right: 27px;

      .infographic__item-line-tablet {
        width: 100px;
      }
    }
    &:nth-child(3) {
      right: -35px;
      top: 55px;
      .infographic__item-line-tablet {
        width: 55px;
      }
    }
    &:nth-child(4) {
      top: 80px;
      .infographic__item-line-tablet {
        width: 28px;
      }
    }
    &:nth-child(5) {
      top: 185px;
      .infographic__item-line-tablet {
        width: 30px;
      }
    }
    &:nth-child(6) {
      right: 20px;
      top: 200px;
      .infographic__item-line-tablet {
        width: 66px;
      }
    }
  }
  &__item-title-tablet{
    margin-right: 4px;
    font-family: var(--third-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    letter-spacing: -0.04em;
    color: var(--slate-950);
  }
  &__item-line-tablet{
    width: 50px;
    height: 2px;
    position: relative;
    background-image: url(../img/line.svg);
    &::before {
      content: "";
      background-repeat: no-repeat;
      background-image: url(../img/circle.png);
      width: 32px;
      height: 32px;
      background-size: contain;
      position: absolute;
      top: 50%;
      right: -4px;
      transform: translate(100%, -50%);
    }
  }
}

.infographic-number {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
  letter-spacing: -0.04em;
  color: var(--slate-950);
  display: none;
  @include tablet {
    display: block;
  }
}
