// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "DIN Pro", sans-serif;
  --second-family: "Inter", sans-serif;
  --third-family: "FONTSPRING DEMO - All Round Gothic Medium", sans-serif;
  --content-width: 1520px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors

  --black: #000;
  --white: #fff;
  --slate-50: #f8fafc;
  --slate-100: #f1f5f9;
  --slate-200: #e2e8f0;
  --slate-300: #cbd5e1;
  --slate-400: #94a3b8;
  --slate-500: #64748b;
  --slate-600: #475569;
  --slate-700: #334155;
  --slate-800: #1e293b;
  --slate-900: #0f172a;
  --slate-950: #020617;
  --blue---0: #fff;
  --blue---50: #ecf5fb;
  --blue---100: #daedf7;
  --blue---150: #c7e4f3;
  --blue---200: #b5dbef;
  --blue---250: #a3d2eb;
  --blue---300: #91c0db;
  --blue---350: #7faecb;
  --blue---400: #6e9dbb;
  --blue---450: #5c8bab;
  --blue---500: #4b7a9c;
  --blue---550: #3e6f90;
  --blue---600: #326483;
  --blue---650: #255a77;
  --blue---700: #194e6b;
  --blue---750: #0c435f;
  --blue---800: #003953;
  --blue---850: #002f43;
  --blue---900: #012434;
  --blue---950: #021a24;
  --blue---1000: #031015;
  --white: #fff;
  --zinc-700: #3f3f46;
  --zinc-400: #a1a1aa;
}
