.banner {
  margin-bottom: 64px;
  @include small-tablet {
    margin-bottom: 36px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    padding: 40px;
    border-radius: 24px;
    height: 600px;
    width: 100%;
    background-repeat: no-repeat;
    @include tablet {
      padding: 20px;
      height: 530px;
      background-position: center;
    }
  }
  &__text {
    max-width: 400px;
    @include tablet {
      max-width: unset;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 36px;
    line-height: 100%;
    color: var(--blue---0);
    @include small-tablet {
      font-size: 20px;
      line-height: 140%;
    }
  }

  &__desk {
    margin: 0;
    margin-bottom: 32px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 144%;
    color: var(--blue---0);
    @include small-tablet {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
    }
  }
  &__link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: var(--blue---0);
    padding: 12px 24px;
    border-radius: 16px;
    background: var(--blue---500);
    @include tablet {
      width: 100%;
      text-align: center;
    }
  }
}
