.partner {
    margin-bottom: 96px;

    @include tablet{
      margin-bottom: 48px;
    }
    &__content {
        padding: 40px;
        border-radius: 24px;
        background: #f0f4f7;
        display: flex;
        gap: 48px;
        align-items: end;
        @include tablet{
            padding: 20px;
            gap: 24px;
            flex-direction: column;
            align-items: center;
        }
    }

    &__link{
    display: inline-block;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    color: white;
    padding: 12px 24px;
    border-radius: 16px;
    background: var(--blue---500);
    @include tablet{
        display: flex;
        justify-content: center;
    }
    }

    &__text{

        @include tablet{
            order: 2;
        }

    }

    &__title {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 36px;
        line-height: 100%;
        color: var(--blue---1000);
        margin: 0;
        margin-bottom: 20px;
        @include tablet{
            font-size: 20px;
            line-height: 140%;
            margin-bottom: 12px;
        }
    }



    &__desk {
        max-width: 480px;
        margin: 0;
        margin-bottom: 32px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 18px;
        line-height: 144%;
        color: #434447;
        @include tablet{
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            color: var(--blue---1000);
            margin-bottom: 20px;
        }
    }
    &__img{
        display: flex;
        justify-content: center;
        border-radius: 24px;
        @include tablet{
            order: 1;
            width: 100%;
        }
    }

}

    .reverse{
        background-color: transparent;
        justify-content: space-between;
        align-items: center;
        flex-direction: row-reverse;
        @include tablet{
            flex-direction: column-reverse;
        }
    }

    .title-about{
        color: var(--blue---650);
    }
