.benefits {
    margin-bottom: 96px;
    @include small-tablet{
        margin-bottom: 48px;
    }
    &__title {
        margin: 0 auto;
        text-align: center;
        margin-bottom: 40px;
        max-width: 720px;
        @include small-tablet{

            text-align: left;
            margin-bottom: 24px;
        }
    }


    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
        @include tablet{
            gap: 16px;
            margin-right: -20px;
            display: flex;
            flex-wrap: nowrap;
            overflow-x: auto;
            -ms-overflow-style: none;  /* Internet Explorer 10+ */
            scrollbar-width: none;  /* Firefox */

        }
    }



    &__item {
        border-radius: 24px;
        height: 550px;
        padding: 32px;
        display: flex;
        justify-content: flex-end;
        flex-direction: column;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        @include tablet{
            background-position: top;
            flex: 0 0 calc(100% / 1.1 - 10px);
        }
        @include mobile{
            padding: 20px;
            height: 400px;
        }
    }



    &__item-title {
        margin: 0;
        margin-bottom: 12px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 30px;
        line-height: 133%;
        color: var(--white);
        @include small-tablet{
            font-size: 20px;
            line-height: 140%;
        }
    }



    &__item-desk {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: var(--white);
        @include small-tablet{}
        color: var(--slate-100);
    }
}
